
import { defineComponent, PropType } from 'vue';

export default defineComponent({
  name: 'ModalError',
  props: {
    visible: {
      type: Boolean as PropType<boolean>,
      default: false,
    },
  },
  emits: ['hide'],
  methods: {
    hideModal() {
      this.$emit('hide');
    },
  },
});
